@charset "utf-8";

@font-face {
  font-family: 'Pretendard-Regular';
  src: url('https://fastly.jsdelivr.net/gh/Project-Noonnu/noonfonts_2107@1.1/Pretendard-Regular.woff') format('woff');
  font-weight: 400;
  font-style: normal;
}

@font-face {
  font-family: 'DungGeunMo';
  src: url('https://fastly.jsdelivr.net/gh/projectnoonnu/noonfonts_six@1.2/DungGeunMo.woff') format('woff');
  font-weight: normal;
  font-style: normal;
}

body {
  margin: 0;
  padding: 0;
  font-family: 'Pretendard-Regular';
  font-weight: 400;
  font-style: normal;
  background: #131313;
}

ul,
li {
  list-style: none;
  padding: 0;
  margin: 0;
}

p{
  color: #333;
}

.App {
  max-width: 1280px;
  text-align: center;
  margin: 0 auto;
  padding: 0 20px;
  box-sizing: border-box;
}

.App>div {
  margin: 60px auto;
}

button {
  border: 1px solid rgba(129, 251,80,1);;
  border-radius: 20px;
  background: none;
  color: rgba(129, 251,80,1);
  padding: 8px 20px;
  cursor: pointer;
  margin-bottom: 20px;
}

h2 {
  font-family: 'DungGeunMo';
  font-weight: 700;
  font-style: normal;
  font-size: 2.5rem;
  margin-bottom: 20px;
  padding-bottom: 0;
  color: rgba(129, 251,80,1);
}

.random-result {
  border: 2px dotted rgba(129, 251,80,1);
  margin: 0 auto;
  border-radius: 20px;
  background: rgba(255, 255, 255, 1);

}

.irr_box .random-result {
  text-align: left;
  padding: 20px;
}

.ran_title {
  font-size: 1rem;
  font-weight: bold;
  margin: 0;
  padding: 0;
  margin-bottom: 4px;
}

.ran_desc {
  font-size: 0.8rem;
  margin: 0;
  padding: 0;
  margin-bottom: 16px;
}
.lucky-number{
  margin: 0 auto;
}
.lucky-number .lucky-wrap{
  max-width: 480px;
  padding: 0 20px;
  display: flex;
  gap: 20px;
  margin: 0 auto;
  justify-content: space-between;
}

.lucky-wrap p{
  margin: 0;
  margin-top: 20px;
  padding: 0;
}
.lucky-text{
  width: 80px;
  height: 80px;
    border-radius: 60px;
  font-size: 40px;
  font-weight: bold;
  background-color:rgba(129, 251,80,1);
  vertical-align: center;
  line-height: 80px;
}